:root {
  --primary-color: #2e3f8b;
  --secondary-color: #000000;
  --white: #FFFFFF;
  --large: 2em;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.contact-bar {
  background: var(--primary-color);
  text-align: right;
  padding: 0.5em;
}

.link {
  color: var(--white);
  display: inline-block;
  text-decoration: none;
  margin-right: 3em;
  font-weight: bold;
}
.nav-bar {
  align-items: center;
  display: flex;
  justify-content: space-around;
  height: 6em;
  padding: 1em;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 200ms;
  transition: all 200ms;
}
.small-nav-bar {
  height: 4em;
  -webkit-transform: scale(0.75);
          transform: scale(0.75);
}
.nav-link {
  color: var(--secondary-color);
  font-size: var(--large);
  text-decoration: none;
}
.nav-link:hover, .nav-link-active {
  color: var(--primary-color);
}
.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: var(--white);
}
.splash-container {
  background-image: url(/gemini-group/static/media/splash.bb9da920.jpg);
  background-size: cover;
  background-attachment: fixed;
  color: var(--white);
  padding: 4em;
}
.splash-title {
  font-size: 3em;
  margin-bottom: 0.5em;
}
.splash-subtitle {
  background: rgba(0, 0, 0, 0.5);
  display: inline-block;
  font-size: 2em;
  margin-bottom: 1em;
  padding: 0.1em 0.5em;
}
.splash-button {
  background: var(--primary-color);
  color: var(--white);
  display: inline-block;
  font-size: 1.5em;
  margin: 0 0.25em;
  padding: 0.5em 1em;
  text-decoration: none;
}
.register {
  padding: 1em;
  text-align: center;
}
.button {
  background: var(--primary-color);
  color: var(--white);
  cursor: pointer;
  display: inline-block;
  font-size: 1.5em;
  margin: 0 0.25em;
  padding: 0.5em 1em;
  text-decoration: none;
}
